.button {
   display: flex;
   justify-content: center;
   align-items: center;
   margin: 5px;
   width: 3em;
   height: 3em;
   border-radius: 100%;
   font-size: 1.5em;
   font-weight: 700;
   color: white;
   background-color: #4a464b;
   user-select: none;
}

.button:hover {
   cursor: pointer;
   border: 0.5px solid #f1f1f2;
}

.operator { background-color: #095077; }

.equals { background-color: #282c58; }

.func { background-color: #f2a20d; }

.wider {
   width: 6.2em;
   border-bottom-left-radius: 45% 100%;
   border-bottom-right-radius: 45% 100%;
   border-top-left-radius: 45% 100%;
   border-top-right-radius: 45% 100%;
}

@keyframes shadowFadeOut {
   from { box-shadow: 0px 0px 8px 5px rgb(82, 132, 159, 0.8); }
   to { box-shadow: none;}
}
