.app {
   width: 100%;
   height: 100vh;
   background-color: #c4dfe6;
}

.calculator-container {
   position: absolute;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
   justify-content: center;
   align-items: center;
   display: flex;
   flex-wrap: wrap;
   width: 90%;
   height: 100%;
}

.calculator {
   width: 350px;
   height: 550px;
   /* position: absolute;
   left: 50%;
   transform: translateX(-50%); */
   margin-left: 5%;
   padding: 5px;
   border: 1px solid #333333;
   border-radius: 15px;
   background-color: #0d0d0d;
   box-shadow: 10px 13px 8px 1px rgba(40,40,40,0.8);
}

.row {
   width: 100%;
   display: flex;
   justify-content: center;
}

.input {
   display: flex;
   justify-content: flex-end;
   align-items: center;
   height: 2.5em;
   margin-bottom: 15px;
   padding: 15px;
   font-size: 2.7em;
   font-weight: bold;
   color: #ffffff;
   border-bottom: 1px solid white;
   /* background-color: #fff; */
   /* outline: 1px solid #888888; */
}
