
.api-button-container {
   margin: 70px;
}

.api-button {
   width: 130px;
   height: 70px;
   margin-bottom: 40px;
   display: flex;
   justify-content: center;
   align-items: center;
   font-size: 1.5em;
   border: 1px solid #333333;
   border-radius: 15px;
   color: white;
   background-color: #0d0d0d;
   box-shadow: 5px 6px 8px 1px rgba(40,40,40,0.8);
}

.api-button:hover {
   cursor: pointer;
   color: #f2a20d;
   border: 0.5px solid #f1f1f2;
   box-shadow: 5px 6px 8px 1px rgb(40, 44, 88, 0.8);
}
